<template>
  <div class="ue-upload-file">
    <div class="vuf-drop-area"
         @click="handleClick"
         @dragleave="handleDragLeave"
         @dragover="handleDragOver"
         @dragenter="handleDragEnter"
         @drop="handleDrop">
      <i class="vuf-icon1" v-show="loading != 1">
        <i class="vuf-icon1-arrow"></i>
        <i class="vuf-icon1-body"></i>
        <i class="vuf-icon1-bottom"></i>
      </i>
      <span class="vuf-hint" v-show="loading !== 1">{{ lang.hint }}</span>
      <span class="vuf-loading" v-show="loading === 1">{{ lang.loading }}</span>
      <div class="vuf-progress-wrap" v-show="loading === 1">
        <span class="vuf-progress" :style="progressStyle"></span>
      </div>
      <span class="vuf-no-supported-hint" v-show="!isSupported">{{ lang.noSupported }}</span>
      <input type="file" v-show="false" @change="handleChange" ref="fileinput">
    </div>
    <div class="vuf-error" v-show="hasError">
      <i class="vuf-icon2"></i>
      {{ errorMsg }}
    </div>
    <div class="vuf-success" v-show="loading === 2">
      <i class="vuf-icon3"></i>
      {{ lang.success }}
    </div>
  </div>
</template>

<script>
import Log from '../../utils/Log'
export default {
  name: 'ueUpload',
  props: {
    // name
    scope: {
      type: String,
      'default': 'upload'
    },
    // 
    isShow: {
      'default': true
    },
    // 
    action: {
      type: String,
      'default': ''
    },
    // 
    params: {
      type: Object,
      'default': {}
    },
    //  kb
    maxSize: {
      type: Number,
      'default': 2048
    },
    //   |xls|xlsx|sheet|vnd.openxmlformats-officedocument.spreadsheetml.sheet|
    fileType: {
      'default': null
    },
    //  false 
    multiFile: {
      'default': false
    }
  },
  data () {
    let isSupported = true

    let lang = {
      hint: '',
      loading: '',
      noSupported: 'IE10',
      success: '',
      fail: '',
      error: {
        multiFile: '',
        outOfSize: ' '
      }
    }
    if (typeof FormData !== 'function') {
      isSupported = false
    }
    return {
      loading: 0, // 0 1 2 3
      lang,
      isSupported,
      hasError: false,
      files: '',
      progress: 0,
      errorMsg: ''
    }
  },
  computed: {
    progressStyle () {
      let {progress} = this
      return {
        width: progress + '%'
      }
    }
  },
  watch: {
    'isShow': function (newValue) {
      if (newValue) {
        this.reset()
      }
    }
  },
  methods: {
    handleDragLeave (e) {
      e.preventDefault()
    },
    handleDrop (e) {
      e.preventDefault()
      if (this.loading !== 1) {
        let files = e.dataTransfer.files
        this.reset()
        if (this.checkFiles(files)) {
          this.upload(files)
        }
      }
    },
    handleDragEnter (e) {
      e.preventDefault()
    },
    handleDragOver (e) {
      e.preventDefault()
    },
    handleClick (e) {
      if (this.loading !== 1) {
        if (e.target !== this.$refs.fileinput) {
          e.preventDefault()
          this.$refs.fileinput.click()
        }
      }
    },
    handleChange (e) {
      if (this.loading !== 1) {
        let files = e.target.files
        this.reset()
        if (this.checkFiles(files)) {
          this.upload(files)
        }
      }
    },
    checkFiles (files) {
      let that = this

      let {lang, maxSize, fileType, multiFile} = that

      let fileNum = files.length
      // 
      if (fileNum < 1) {
        this.$Notice.info({
          title: '',
          desc: lang.error.multiFile
        })
        return false
      }
      if (!fileType) {
        return true
      }
      let fileType_ = ('|' + fileType.join('|') + '|').replace('xlsx', 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet')
      // 
      if (!multiFile) {
        if (fileNum > 1) {
          this.$Notice.error({
            title: '',
            desc: lang.error.multiFile
          })
          return false
        }
        // 
        let file = files[0]
        let _fileType = file.type
        if (!_fileType) {
          _fileType = file.name.substr(file.name.indexOf('.') + 1)
        }
        Log.d(_fileType)
        let type = '|' + _fileType + '|'
        if (fileType_ && fileType_.indexOf(type) < 0) {
          this.$Notice.error({
            title: '',
            desc: '' + fileType.join(',') + ''
          })
          return false
        }

        // 
        let maxSizeM = file.size / 1024
        if (maxSizeM > maxSize) {
          this.$Notice.error({
            title: '',
            desc: lang.error.outOfSize + '[' + Math.round(maxSizeM, 2) + 'KB, ' + maxSize + 'KB]'
          })
          return false
        }
      } else {
        for (let i = 0; i < fileNum; i++) {
          // 
          let file = files[0]
          let _fileType = file.type
          if (!_fileType) {
            _fileType = file.name.substr(file.name.indexOf('.') + 1)
          }
          let type = '|' + _fileType + '|'
          if (fileType_ && fileType_.indexOf(type) < 0) {
            this.$Notice.error({
              title: '',
              desc: '' + fileType.join(',') + ''
            })
            return false
          }
          // 
          let maxSizeM = file.size / 1024
          if (maxSizeM > maxSize) {
            this.$Notice.error({
              title: '',
              desc: lang.error.outOfSize + '[' + Math.round(maxSizeM, 2) + 'KB, ' + maxSize + 'KB]'
            })
            return false
          }
        }
      }
      return true
    },
    reset () {
      let that = this
      that.loading = 0
      that.hasError = false
      that.errorMsg = ''
      that.progress = 0
    },
    upload (files) {
      let that = this

      let {action, params, multiFile, scope} = this

      let fmData = new FormData()
      // 
      if (!multiFile) {
        fmData.append(scope, files[0])
      } else {
        fmData.append(scope, files)
      }
      // 
      if (typeof params === 'object' && params) {
        Object.keys(params).forEach((k) => {
          fmData.append(k, params[k])
        })
      }
      // 
      const uploadProgress = function (event) {
        if (event.lengthComputable) {
          that.progress = 100 * Math.round(event.loaded) / event.total
        }
      }
      // 
      that.loading = 1
      new Promise(function (resolve, reject) {
        let client = new XMLHttpRequest()
        client.open('POST', action, true)
        client.onreadystatechange = function () {
          if (this.readyState !== 4) {
            return
          }
          if (this.status === 200) {
            resolve(this.responseText)
          } else {
            reject(this.responseText)
          }
        }
        client.upload.addEventListener('progress', uploadProgress, false) // 
        client.send(fmData)
      }).then(
        // 
        function (resData) {
          that.files = ''
          that.loading = 0
          that.$Notice.success({
            title: '',
            desc: ''
          })
        },
        // 
        function (resData) {
          Log.d(resData)
          that.loading = 0
          that.files = ''
          let message = ''
          if (resData && resData['message']) {
            message = resData['message']
          }
          that.$Notice.error({
            title: '',
            desc: message
          })
        }
      )
    }
  }
}
</script>

<style scoped>
    @import "./css/upload.css";
</style>



